import React from 'react';
import { useHistory } from "react-router-dom";

function Header() {

    const history = useHistory();

    return (
        <>
            <header>
                <div>
                    <div className="back" onClick={() => { history.push("/") }}></div>
                    <h1>FULL-STACK <span>FAVORITES</span></h1>
                </div>
                
            </header>
        </>
    );
}

export default Header;